@charset "utf-8";
/*////////////////////////////////////////////////////

	ページごとの個別スタイル
	※サイト規模によってはさらにファイルを分けること

////////////////////////////////////////////////////*/

/* ------------------------------------------------
	トップページここから
------------------------------------------------ */
.l-box{
  opacity:0;
  @include transition(all,.5s .0s, ease);
  @include flexbox;
  section{
    @include flexbox;
  }
}



.tab-box{
  margin: 100px auto 0 auto;
  @include flexbox;
  @include flex-justify-content($justify:center);
}

.tab-group{
  @include flexbox;
  @include flex-justify-content($justify:center);
  @include flex-align($align: center);
  width:60%;
  height:86px;
  .tab{
    font-size:var(--fSize);
    font-weight:bold;
    flex-grow: 1;
    list-style:none;
    // border-top:solid 1px #CCC;
    // border-left:solid 1px #CCC;
    cursor:pointer;
    height:86px;
    background:var(--bg-color01);
    @include flexbox;
    @include flex-justify-content($justify:center);
    @include flex-align($align: center);
    // background:var(--bg-color04);
    position:relative;
    top:1px;
    z-index:1;
    @include transition($property: all, $time: .5s, $timing: cubic-bezier(0, 0.49, 0.05, 0.99));
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    img{
      position:relative;
      left:-10px;
    }
    &:nth-child(1){
      // border-bottom:solid 1px var(--thema-color01);
      img{
        top:-4px;
      }
    }
    &:nth-child(2){
      // border-bottom:solid 1px var(--thema-color01);
    }
    &:hover{
      cursor:pointer;
      @include opacity(0.6);
    }
  }
}

.tab-box-inner{
  padding:80px 0;
  
}

.panel-group{
  height:auto;
}
.panel{
  transition: all .5s ease-out;
  display:none;
}
.tab.is-active{
  background:var(--bg-color03);
  color:var(--font-color01);
  transition: all .5s ease-out;
    &:hover{
      @include opacity(1);
    }
  
}
.tab-A.tab-border{
  border-top:solid 1px var(--thema-color01);
  border-bottom:solid 1px var(--thema-color01);
  border-left:solid 1px var(--thema-color01);
  border-right:solid 1px var(--thema-color01);
  @include box-sizing;
}

.tab-B.tab-border{
  border-top:solid 1px var(--thema-color01);
  border-bottom:solid 1px var(--thema-color01);
  border-right:solid 1px var(--thema-color01);
  @include box-sizing;
}

.tab-A.is-active,.tab-B.is-active{
  // border-top:solid 1px var(--thema-color01);
  // border-left:solid 1px var(--thema-color01);
  // border-right:solid 1px var(--thema-color01);
   border-bottom:none;
}

.panel.is-show{
  display:block;
  transition: all .5s ease-out;
}

.tag-box{
  @include flexbox;
  // @include flex-justify-content($justify:space-between);
  @include flex-direction($direction:row);
  @include flex-align($align: center);
  @include flex-wrap($wrap:wrap);
  column-gap:20px;
  row-gap:20px;
  li{
    background:var(--bg-color03);
    border-radius:10px;
    font-size:var(--fSize);
    height:20px;
    padding:8px 20px 10px 20px;
    // &:before{
    //   display: block;
    //   content:"";
    //   width: 100px;
    // }
  }
}





.mv-box{
  display:grid;
  grid-template-columns:1fr 50% 1fr;  
  width:100%;  
  column-gap:60px;     
}
.mv-inner-c{
  @include flexbox;
  @include flex-justify-content($justify:space-between);
  @include flex-align($align: center);
  column-gap:40px;
  .c-img{
    img{
      max-width: 100%;
      height: auto;
    }
  }
  .c-txt{
    img{
      max-width: 100%;
      height:auto;
    }
  }
}

.mv-inner-l{
    @include flexbox;
    @include flex-direction($direction:column);
    // @include flex-justify-content($justify:space-between);
    // @include flex-align($align: center);
    .mv-img-box01{
      @include flexbox;
      @include flex-justify-content($justify:space-between);
      @include flex-align($align: center);
      
    }
  div{
    img{
      max-width: 100%;
      height:auto;
    }
  }
}

.mvanimation{
    @include animation(mvtxt01 2s cubic-bezier(0, 0.58, 0.25, 1) 0 1 normal);
    @include keyframes(mvtxt01) {
      0% {
        opacity:0;
        top: 35px;
      }
      20% {
        opacity:1;
        top: -18px;
      }
      80% {
        top: -18px;
        opacity:1;
      }
      100% {
        top: -50px;
        opacity:0;
      }
    }
}


/* ------------------------------------------------
  使い回しアイテム
------------------------------------------------ */

.category-tag{
    @include flexbox;
    @include flex-justify-content($justify:flex-start);
    @include flex-wrap($wrap:wrap);
    column-gap:15px;
    row-gap:5px;
  .detail-aside & {
    margin-top:0px;
    padding:8px 0 8px 0;
  }
  li{
    font-size:var(--fSize);
    position:relative;
    @include english;
    // &:before{
    //   content:'';
    //   display:inline-block;
    //   position:absolute;
    //   background:var(--font-color05);
    //   width:10px;
    //   height:2px;
    //   top:-5px;
    //   left:0px;
    // }
    a{
      color:var(--font-color01);
    }
  }
}
.type-tag{
  margin-top: auto;
  line-height: 1;
  @include flexbox;
  column-gap:8px;
  border-top: 1px solid var(--bg-color07);
  padding-top: 20px;
  @include flex-justify-content($justify:flex-end);
  @include flex-wrap($wrap:wrap);
  row-gap:22px;
  li{
    font-size:var(--fSize);
    @include english;
    
    a{
      color:var(--font-color01);
    }    
    &:last-child{
      margin-right:0;
    }
  }
  .detail-aside & {
    border:none;
    padding:0px 0 16px 0;
    margin-top:10px;
    @include flex-justify-content($justify:flex-start);
  }
}


/* ------------------------------------------------
  一覧ページここから
------------------------------------------------ */

.entry-box{
  margin: 140px 0 180px 0;
  .l-list & {
    margin: 80px 0 80px 0;
  }
  @include media(sp){
    margin: 40px 0 40px 0;
  }
}

.inner-entry-box{
  width:70%;
  margin:0 auto;
  font-size:var(--Enfont);
  @include media(tablet){
    width:90%;
  }
  @include media(sp){
    width:100%;
  }
  .date{
    font-size:var(--fSize);
    color:var(--font-color02);
    margin-bottom:30px;
    @include media(sp){
      margin-bottom:10px;
    }
  }
}



/* ------------------------------------------------
