      @include english;
      // padding:7px 0 ;
      // display:table;
      width:100%;
      margin-bottom:10px;
      &:nth-child(odd){
        background:var(--bg-color01);
      }
    }
    .agenda{
      font-size:var(--fSize);
      @include english($weight: bold);
      display:block;
      // width:120px;
      position:relative;
      // display:table-cell;
      // vertical-align: middle;
      // &:after{
      //   content:'-';
      //   display:inline-block;
      //   position:absolute;
      //   left:84px;
      //   top:50%;
      //   @include translate ($x:0, $y:-54%);
      //   font-size:var(--fSizeS);
      //   font-weight:normal;
      //   color:var(--font-color02);
      //   margin:0 10px;
      // }
    }
  }
  .detail-link{
    font-size:var(--fSize);
    margin:30px auto 0 auto;
    width:60%;
  }
}

.content-movie {
  margin-bottom:30px;

}

.movie{
  position: relative;
  padding-bottom: 56.25%; /* アスペクト比16:9の場合 */
  padding-top: 30px;
  height: 0;
  overflow: hidden;
  // margin-bottom: 2rem; /* YouTubeの下に余白をつける */
  border:1px solid var(--font-color01);
  iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding:10px;
    @include box-sizing;
  }
}
.detain-innerbox{
  margin-bottom:40px;
  &:last-child{
    margin-bottom:0;
  }
}

/* ------------------------------------------------
  記事ページ 同じアーティストの曲
------------------------------------------------ */
.detail-box-same{
  margin-top:80px;
}
.detail-box-contents{

  width:100%;
  display:grid;
  justify-content:center;
  grid-template-columns: repeat(auto-fill, minmax(280px,1fr));
  grid-template-rows: auto;
  column-gap:30px;
  row-gap:40px;
  margin-top:40px;
  @include media(sp){
    grid-template-columns: repeat(auto-fill, minmax(45%,1fr));
    column-gap:25px;
    margin-top:var(--spaceL);
  }
  @include media(spMS){
    grid-template-columns: 1fr;
    // column-gap:25px;
    margin-top:var(--spaceL);
  }
}

/* ------------------------------------------------
  記事ページ 似た曲の表示設定
------------------------------------------------ */

.similar-box{
  background:var(--bg-color03);
  padding:10rem 0;
  @include media(sp){
    margin:40px 0 60px 0;
  }
  .similar-box-inner{
    margin:100px auto 0 auto;
    // @include flexbox;
    // @include flex-wrap($wrap:wrap);
    display:grid;
    justify-content:center;
    grid-template-columns: repeat(auto-fill, minmax(280px,1fr));
    grid-template-rows: auto;
    column-gap:40px;
    row-gap:40px;
    width:var(--cn-width);
    .l-box{
      // width:25%;
    }
    @include media(sp){
      @include flex-justify-content($justify:space-between);
      column-gap:2%;
      .l-box{
        width:48%;
        margin-bottom:40px;
      }
    }
  }
}

.crp-list-title{
  display:none;
}

/* ------------------------------------------------
  検索件数
------------------------------------------------ */
.number{
  display:none;
}


/* ------------------------------------------------
  404
------------------------------------------------ */
.not-found{
  @include flexbox;
  @include flex-align($align: center);
  @include flex-justify-content($justify:center);
  @include flex-direction($direction:column);
  margin:120px 0 40px 0;
  // row-gap:40px;
}

.not-found-text01{
  font-size:var(--font404);
  color:var(--font-color04);
  font-weight:bold;
  line-height:1;
}


.not-found-text02{
  font-size:var(--MVfont);
  color:var(--font-color04);
  font-weight:bold;
  line-height:1;
}

.not-found-link{

  margin-top:60px;

  // background:var(--bg-color02);
  border:1px solid var(--font-color01);
  a{
    @include flexbox;
    @include flex-align($align: center);
    @include flex-justify-content($justify:center);
    // display:inline-block;
    width:300px;
    height:60px;
    color:var(--font-color01);
  }
}













